:root {
  font-size: 62.5%;
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;
  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.8705882353);
  background-color: #242424;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}
@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

.root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

img {
  display: block;
  max-width: 100%;
  object-fit: cover;
}

video {
  max-width: 100%;
  display: block;
}

a {
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8705882353);
  text-decoration: inherit;
}
@media (prefers-color-scheme: light) {
  a {
    color: #213547;
  }
}
a:hover {
  color: rgba(204, 204, 204, 0.8705882353);
}
@media (prefers-color-scheme: light) {
  a:hover {
    color: #41698d;
  }
}

body {
  margin: 0;
  min-height: 100vh;
}

h1 {
  font-size: 4.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.6rem;
}

h1, h2, h3 {
  margin-bottom: 3rem;
  line-height: 1.1;
}

p, a {
  font-size: 2rem;
}

[class$=__container] {
  padding-block: 5rem;
  overflow: hidden;
}

[class$=__wrapper] {
  width: 90%;
  max-width: 120rem;
  margin-inline: auto;
}

::-webkit-scrollbar {
  width: 0.5rem;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 0.6rem #0b0b0b;
  border-radius: 10px;
}
@media (prefers-color-scheme: light) {
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 0.5rem gray;
  }
}

::-webkit-scrollbar-thumb {
  background: #8a8a8a;
  border-radius: 1rem;
}

span {
  font-size: 2rem;
}

.button {
  display: block;
  background-color: rgba(255, 255, 255, 0.8705882353);
  color: #213547;
  text-align: center;
  margin-top: 2rem;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .button:hover {
    background-color: rgba(242, 242, 242, 0.8705882353);
    color: #213547;
  }
}
@media (prefers-color-scheme: light) {
  .button {
    display: block;
    background-color: #213547;
    color: #ffffff;
    text-align: center;
    margin-top: 2rem;
    padding: 0.5rem 2rem;
    border-radius: 0.5rem;
    font-size: 1.6rem;
  }
}
@media (prefers-color-scheme: light) and (min-width: 768px) {
  .button:hover {
    background-color: #192836;
    color: #ffffff;
  }
}
.button-center {
  max-width: 30rem;
  margin-inline: auto;
  margin-top: 3rem;
}
.button-left {
  max-width: 30rem;
}

.icon {
  width: 2.8rem;
  color: #c6cdf8;
  stroke: #c6cdf8;
}

.header {
  position: relative;
  min-height: 40rem;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  color: rgba(255, 255, 255, 0.8705882353);
  overflow: hidden;
}
@media (min-width: 768px) {
  .header {
    height: 50rem;
  }
}
@media (min-width: 3840px) {
  .header {
    height: 90rem;
  }
}
.header__content {
  background-image: linear-gradient(rgba(33, 53, 71, 0.8), transparent);
  height: 100%;
  width: 100%;
}
.header__background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__flex {
    height: 100%;
    justify-content: center;
  }
}
.header__heading {
  text-align: center;
}
.header__heading-span {
  display: block;
  font-size: 2.5rem;
  margin-top: 2rem;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
}
.header__text {
  text-align: center;
}
.header-secundary {
  height: 35rem;
}
.header__services {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .header__services {
    flex-direction: row;
    align-items: unset;
    justify-content: center;
  }
}
.header__service {
  font-size: 1.8rem;
  font-family: "Montserrat", sans-serif;
}

.footer {
  text-align: center;
  background-color: #213547;
}
@media (prefers-color-scheme: light) {
  .footer {
    background-color: #242424;
  }
}
@media (prefers-color-scheme: light) {
  .footer__title {
    color: rgba(255, 255, 255, 0.8705882353);
  }
}
.footer__text {
  margin-bottom: 3rem;
}
@media (prefers-color-scheme: light) {
  .footer__text {
    color: rgba(255, 255, 255, 0.8705882353);
  }
}
.footer__icons {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.footer__icon {
  width: 5rem;
}
.footer__copyright {
  margin-top: 5rem;
  font-weight: 700;
}
@media (prefers-color-scheme: light) {
  .footer__copyright {
    color: rgba(255, 255, 255, 0.8705882353);
  }
}

.bar {
  padding-block: 1rem;
  transition-property: background-color;
  transition-duration: 0.2s;
  z-index: 100;
}
@media (min-width: 768px) {
  .bar {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
  }
}
.bar__icon {
  display: flex;
  justify-content: flex-end;
  padding: 1rem;
}
@media (min-width: 768px) {
  .bar__icon {
    display: none;
  }
}
@media (min-width: 768px) {
  .bar__flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.bar__logo {
  filter: invert(1);
  max-width: 25rem;
}
@media (prefers-color-scheme: light) {
  .bar__logo {
    filter: unset;
  }
}
@media (min-width: 768px) {
  .bar-scrolled {
    background-color: #242424;
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  }
}
@media (min-width: 768px) and (prefers-color-scheme: light) {
  .bar-scrolled {
    background-color: #ffffff;
  }
}

.navBar {
  display: none;
}
.navBar.showNav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navBar {
    display: flex;
    flex-direction: row;
  }
  .navBar.showNav {
    flex-direction: row;
  }
}
.navBar__link {
  color: rgba(255, 255, 255, 0.8705882353);
  padding: 1rem 2rem;
}
.navBar__link:hover {
  color: currentColor;
}
@media (prefers-color-scheme: light) {
  .navBar__link-scrolled {
    color: #213547;
  }
  .navBar__link-scrolled:hover {
    color: #213547;
  }
}

.active {
  background-color: rgba(255, 255, 255, 0.8705882353);
  color: #213547;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}
.active:hover {
  color: #213547;
}
@media (prefers-color-scheme: light) {
  .active {
    background-color: #213547;
    color: rgba(255, 255, 255, 0.8705882353);
  }
  .active:hover {
    color: rgba(255, 255, 255, 0.8705882353);
  }
}

.logo {
  display: none;
}
@media (min-width: 768px) {
  .logo {
    display: block;
    width: 25rem;
    height: unset;
    fill: rgba(255, 255, 255, 0.8705882353);
  }
}
@media (min-width: 768px) and (prefers-color-scheme: light) {
  .logo-scrolled {
    fill: #213547;
  }
}

.visit__heading {
  text-align: center;
}
.visit__span {
  display: block;
}
.visit__grid {
  display: grid;
  gap: 4rem;
}
@media (min-width: 1024px) {
  .visit__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .visit__column-left {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
.visit__image {
  height: 40rem;
}
@media (min-width: 768px) {
  .visit__image {
    height: 100%;
    width: 100%;
  }
}
.visit__content {
  margin-bottom: 5rem;
}
.visit__flex {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 2rem;
  scroll-snap-type: x mandatory;
}

.live__heading {
  text-align: center;
}
.live__text {
  font-size: 3rem;
  text-align: center;
}
.live__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}
.live__button {
  display: block;
  background-color: #213547;
  color: white;
  text-align: center;
  margin-top: 2rem;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .live__button:hover {
    background-color: #192836;
    color: white;
  }
}

.property {
  border-radius: 0.5rem;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  text-align: left;
}
.property__graphic {
  position: relative;
  display: flex;
  align-items: flex-end;
  aspect-ratio: 16/9;
  background-image: linear-gradient(to top, rgba(33, 53, 71, 0.6), transparent);
  padding: 2rem;
}
.property__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
}
.property__title {
  font-size: 2.3rem;
}
@media (prefers-color-scheme: light) {
  .property__title {
    color: rgba(255, 255, 255, 0.8705882353);
  }
}
.property__info {
  padding: 2rem;
}
.property__button {
  display: block;
  background-color: rgba(255, 255, 255, 0.8705882353);
  color: #213547;
  text-align: center;
  margin-top: 2rem;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  width: 100%;
}
@media (min-width: 768px) {
  .property__button:hover {
    background-color: rgba(242, 242, 242, 0.8705882353);
    color: #213547;
  }
}
.property__flex {
  justify-content: space-around;
  margin-bottom: 3rem;
}
@media (min-width: 1024px) {
  .property__flex {
    display: flex;
  }
}
.property__icon {
  display: flex;
  gap: 1rem;
}

.tour {
  border-radius: 0.5rem;
  scroll-snap-align: center;
  overflow: hidden;
}
@media (min-width: 768px) {
  .tour {
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  }
}
.tour__graphic {
  position: relative;
}
.tour__img {
  aspect-ratio: 16/9;
  object-fit: cover;
}
.tour__details {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 2rem;
}
.tour__block {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.tour__detail {
  background-color: rgba(33, 53, 71, 0.8);
  padding: 0.2rem 1rem;
  border-radius: 0.5rem;
  text-align: center;
  font-size: 10rem;
  border: 0.1rem solid #1c2d3d;
}
@media (prefers-color-scheme: light) {
  .tour__detail {
    background-color: rgba(255, 255, 255, 0.6705882353);
    border: 0.1rem solid rgba(247, 247, 247, 0.8705882353);
  }
}
.tour__paragraph {
  font-size: 1.3rem;
  font-weight: 700;
}
.tour__paragraph-span {
  display: block;
  background-color: rgba(255, 255, 255, 0.8705882353);
  color: #213547;
  text-transform: uppercase;
  border-radius: 0.3rem;
  padding-inline: 1rem;
}
@media (prefers-color-scheme: light) {
  .tour__paragraph-span {
    background-color: #213547;
    color: rgba(255, 255, 255, 0.8705882353);
  }
}
.tour__info {
  padding: 2rem;
}
.tour__button {
  display: block;
  background-color: rgba(255, 255, 255, 0.8705882353);
  color: #213547;
  text-align: center;
  margin-top: 2rem;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  width: 100%;
}
@media (min-width: 768px) {
  .tour__button:hover {
    background-color: rgba(242, 242, 242, 0.8705882353);
    color: #213547;
  }
}
@media (prefers-color-scheme: light) {
  .tour__button {
    display: block;
    background-color: #213547;
    color: rgba(255, 255, 255, 0.8705882353);
    text-align: center;
    margin-top: 2rem;
    padding: 0.5rem 2rem;
    border-radius: 0.5rem;
    font-size: 1.6rem;
  }
}
@media (prefers-color-scheme: light) and (min-width: 768px) {
  .tour__button:hover {
    background-color: #192836;
    color: rgba(255, 255, 255, 0.8705882353);
  }
}
.tour__title {
  font-size: 2.2rem;
}

.featured {
  margin-top: 5rem;
  color: rgba(255, 255, 255, 0.8705882353);
  background-image: linear-gradient(to left, rgba(33, 53, 71, 0.3), rgba(33, 53, 71, 0.7));
  position: relative;
  height: 80rem;
}
@media (min-width: 3840px) {
  .featured {
    height: 150rem;
  }
}
.featured__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.featured__info {
  height: 100%;
}
.featured__flex {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.featured__property {
  max-width: 50rem;
}
.featured__details {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.featured__icon {
  display: flex;
  gap: 2rem;
}
.featured__price {
  font-weight: 700;
  font-size: 4rem;
}
.featured__button {
  display: block;
  background-color: rgba(255, 255, 255, 0.8705882353);
  color: #242424;
  text-align: center;
  margin-top: 2rem;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .featured__button:hover {
    background-color: rgba(242, 242, 242, 0.8705882353);
    color: #242424;
  }
}

.trust__flex {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .trust__flex {
    flex-direction: row;
  }
}
.trust__image {
  width: 10rem;
  margin: 0 auto;
}
.trust__heading, .trust__text {
  text-align: center;
}
.trust__heading {
  margin-top: 3rem;
}

.secundaryHeader {
  position: relative;
  height: 70rem;
}
.secundaryHeader__content {
  background-image: linear-gradient(rgba(33, 53, 71, 0.8), transparent);
  height: 100%;
}
.secundaryHeader__video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.secundaryHeader__flex {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}